// main page elements
body {
	font-size: 14px;
	color: #000000;
	font-family: 'Graphik LCG';
	font-weight: 400;
	background: white;
	overflow-x: hidden;
 }	// +scrollbar(10px, auto, $yellow, $gray2, $dark, null)

.page-container {
	max-width: 1920px;
	width: 100%;
	margin: 0 auto;
	overflow: hidden; }

.container {
	position: relative;
	max-width: 1170px;
	width: 100%;
	padding: 0;
	margin: 0 auto;
	@include mq($max-width: 1200px) {
		padding: 0 15px;
		max-width: 100%; }
	@include mq($max-width: 575px) {
		padding: 0 10px; } }

h1 {
	font-weight: 700;
	font-size: 70px;
	line-height: 120%; }
h2 {
	font-weight: 700;
	font-size: 48px;
	line-height: 120%;
	position: relative;
	&::after {
		content: '';
		left: -30px;
		top: -20px;
		background: $blue;
		border-radius: 10px;
		width: 30px;
		height: 30px;
		position: absolute; } }
.flex {
	display: flex; }

.btn {
	display: flex;
	align-items: center;
	justify-content: center;
	font-size: 18px;
	line-height: 140%;
	width: 100%; }

.fixed-nav {
	position: fixed;
	top: 80px;
	max-width: 100%;
	width: 100%;
	z-index: 3;
	height: 80px !important; }

.mfp-close {
	top: 25px;
	right: 25px;
	width: 40px;
	height: 40px;
	background: #ECEBE9 !important;
	border-radius: 100%;
	opacity: 1;
	font-size: 34px;
	line-height: 40px;
	&:hover {
		color: white !important;
		background: #000000 !important; } }
.mfp-close-btn-in .mfp-close {
	color: black; }
.mfp-close:active {
	top: 25px; }
.apply-popup {
	margin: 0 auto;
	background: white;
	border-radius: 20px;
	max-width: 640px;
	width: 100%;
	padding: 70px;
	padding-bottom: 60px;
	position: relative;
	h3 {
		font-weight: 700;
		font-size: 36px;
		line-height: 120%;
		margin-bottom: 38px; }
	.form__inputs {
		display: flex;
		flex-direction: column;
		gap: 20px;
		margin-bottom: 40px;
		textarea {
			max-width: 100%;
			background: #F4F4F6;
			border: 1px solid #E3E3E3;
			border-radius: 10px;
			padding: 19px 20px;
			font-weight: 400;
			font-size: 16px;
			line-height: 140%;
			height: 120px;
			min-height: 60px;
			&:focus-within {
				border: 1px solid $blue;
				background: white;
				box-shadow: 0px 0px 20px rgba(63, 43, 255, 0.25); } }
		input {
			background: #F4F4F6;
			border: 1px solid #E3E3E3;
			border-radius: 10px;
			height: 60px;
			max-width: 100%;
			width: 100%;
			padding: 0 20px;
			font-weight: 400;
			font-size: 16px;
			line-height: 140%;
			&:focus-within {
				border: 1px solid $blue;
				background: white;
				box-shadow: 0px 0px 20px rgba(63, 43, 255, 0.25); } } }
	.flex {
		align-items: center;
		justify-content: space-between;
		.btn {
			max-width: 200px;
			height: 60px;
			background: $blue;
			color: white;
			border-radius: 10px;
			&:hover {
				transition: all 0.5s;
				background: black; } }
		.form__prava {
			font-weight: 400;
			font-size: 12px;
			line-height: 140%;
			max-width: 274px;
			a {
				color: $blue;
				text-decoration: underline; } } } }
