.sizes {
	display: flex;
	gap: 30px;
	margin-top: 80px;
	margin-bottom: 190px;
	&__right {
		position: relative;
		max-width: 100%;
		width: 100%;
		h3 {
			font-weight: 900;
			font-size: 24px;
			line-height: 120%;
			color: white;
			margin-bottom: 31px; }
		.sizes__number {
			font-weight: 700;
			font-size: 64px;
			line-height: 120%;
			position: absolute;
			color: transparent;
			-webkit-text-stroke: 1px white; }
		.sizes__info {
			display: flex;
			align-items: center;
			gap: 20px;
			.sizes__info-v {
				height: 50px;
				width: 2px;
				background: white; }
			.sizes__info-left, .sizes__info-right {
				font-weight: 400;
				font-size: 16px;
				line-height: 120%;
				color: white; } }
		.sizes__h1 {
			padding: 40px;
			background: $blue;
			position: absolute;
			top: 0;
			left: 0;
			max-width: 540px;
			width: 100%;
			height: 300px;
			border-radius: 20px;
			&:hover {
				.sizes__h1-dop {
					opacity: 1;
					visibility: visible;
					transition-delay: 0s; }
				svg {
					opacity: 1;
					visibility: visible;
					transition-delay: 0s; } }
			.sizes__number {
				bottom: 23px; }
			svg {
				position: absolute;
				right: 200px;
				top: 0;
				bottom: 0;
				margin: auto;
				opacity: 0;
				visibility: hidden;
				transition: opacity 0.3s, visibility 0s linear 0.3s; }
			img {
				position: absolute;
				right: 0;
				top: 0;
				margin: auto;
				bottom: 0;
				max-width: 255px;
				width: 100%; }
			.sizes__h1-dop {
				opacity: 0;
				visibility: hidden;
				transition: opacity 0.3s, visibility 0s linear 0.3s; } }
		.sizes__block {
			display: flex;
			gap: 30px;
			align-items: flex-end;
			.sizes__h {
				padding: 40px;
				background: $blue;
				position: relative;
				max-width: 540px;
				width: 100%;
				height: 300px;
				border-radius: 20px;
				&:hover {
					.sizes__h1-dop {
						opacity: 1;
						visibility: visible;
						transition-delay: 0s; }
					svg {
						opacity: 1;
						visibility: visible;
						transition-delay: 0s; } }
				.sizes__number {
					bottom: 23px; }
				.sizes__h1-dop {
					opacity: 0;
					visibility: hidden;
					transition: opacity 0.3s, visibility 0s linear 0.3s; }
				svg {
					position: absolute;
					bottom: 20px;
					right: 230px;
					opacity: 0;
					visibility: hidden;
					transition: opacity 0.3s, visibility 0s linear 0.3s; }
				img {
					position: absolute;
					right: 0;
					top: 0;
					margin: auto;
					bottom: 0;
					max-width: 285px;
					width: 100%; } }
			.sizes__v {
				padding: 40px;
				background: $blue;
				position: relative;
				max-width: 300px;
				width: 100%;
				height: 630px;
				border-radius: 20px;
				&:hover {
					.sizes__h1-dop {
						opacity: 1;
						visibility: visible;
						transition-delay: 0s; }
					svg {
						opacity: 1;
						visibility: visible;
						transition-delay: 0s; } }
				.sizes__h1-dop {
					opacity: 0;
					visibility: hidden;
					transition: opacity 0.3s, visibility 0s linear 0.3s; }
				svg {
					opacity: 0;
					visibility: hidden;
					transition: opacity 0.3s, visibility 0s linear 0.3s;
					bottom: 0;
					left: 0;
					right: 0;
					margin: 0 auto;
					position: absolute; }
				.sizes__number {
					top: 30px;
					right: 40px; }
				img {
					bottom: 0;
					left: 0;
					right: 0;
					margin: auto;
					position: absolute;
					max-width: 220px;
					width: 100%; } } } }
	&__left {
		max-width: 270px;
		width: 100%;
		position: relative;
		h2 {
			margin-top: 20px;
			margin-bottom: 32px; }
		p {
			font-weight: 400;
			font-size: 18px;
			line-height: 150%; }
		.btn {
			position: absolute;
			bottom: 0;
			left: 0;
			background: inherit;
			border: 1px solid black;
			font-weight: 400;
			color: black;
			border-radius: 20px;
			max-width: 200px;
			height: 60px;
			&:hover {
				transition: all 0.5s;
				background: black;
				color: white; } } } }

.about {
