.stages {
	margin-bottom: 100px;
	position: relative;
	background: $blue;
	padding-top: 100px;
	overflow-y: hidden;
	&__column {
		display: flex;
		flex-direction: column;
		gap: 20px; }
	&__absolute {
		z-index: 3;
		justify-content: center;
		align-items: center;
