		border-radius: 20px;
		display: flex;
		gap: 52px;
		max-width: 370px;
		height: 145px;
		width: 100%;
		right: -100px;
		top: -30px;
		&::after {
			content: '';
			position: absolute;
			bottom: -20px;
			border: 10px solid transparent;
			border-top: 10px solid #F4F4F6; }
		&-item {
			display: flex;
			align-items: center;
			gap: 20px;
