	img {
		right: -101px;
		position: absolute;
		bottom: 0px;
		z-index: 2; }
	h2 {
		margin-bottom: 82px;
		color: white;
		spab {
			font-weight: 900; }
		&:after {
			background: white; } }
	&__block {
		background: white;
		border-radius: 20px 20px 0px 0px;
		padding: 70px;
		padding-top: 60px;
