			font-size: 72px;
			line-height: 100%;
			letter-spacing: 0.13em;
			color: white;
			-webkit-text-stroke: 1px $blue; }
		p {
			font-weight: 400;
			font-size: 18px;
			line-height: 150%;
			max-width: 400px;
			width: 100%; } }
	.btn {
		background: black;
		max-width: 270px;
		height: 60px;
		color: white;
